en comprender el problema de investigación, contextualizarlo y justificarlo con fundamentos científicos reconocidos. Este componente brinda solidez al trabajo, al establecer un diálogo entre el fenómeno estudiado y el conocimiento existente, lo que facilita su interpretación y análisis en el momento de presentar los resultados. El marco teórico ofrece una perspectiva analítica desde la cual interpretar los hallazgos de la investigación y relacionarlos con el conocimiento disponible. (Creswell & Creswell, 2023, p. 98).")]),_vm._m(36),_vm._m(37),_c('p',{staticClass:"mb-5"},[_vm._v("Si la pregunta de investigación es: ¿Cuáles son los factores que inciden en la deserción escolar de estudiantes entre 12 y 16 años en instituciones rurales del municipio de Riosucio?, el marco teórico podría organizarse en los siguientes ejes:")]),_vm._m(38),_c('p',{staticClass:"mb-5"},[_vm._v("Para su elaboración es fundamental consultar fuentes confiables y actualizadas, como artículos científicos, libros especializados, informes técnicos, bases de datos académicas y documentos institucionales. Un marco teórico bien estructurado permite al investigador sustentar sus decisiones, delimitar el alcance del estudio y orientar el análisis de los resultados.")]),_c('separador'),_vm._m(39),_vm._m(40),_c('p',{staticClass:"mb-5"},[_vm._v("Según Hernández et al. (2014), “la elección de las técnicas de recolección debe ser coherente con los objetivos de estudio, el tipo de datos que se desean obtener y el diseño metodológico adoptado” (p. 148). A continuación, se presenta una tabla que resume las técnicas más comunes según el enfoque de investigación:")]),_vm._m(41),_vm._m(42),_c('p',{staticClass:"mb-5"},[_vm._v("La elección de las técnicas también se ve influida por:")]),_vm._m(43),_c('separador'),_vm._m(44),_vm._m(45),_c('p',{staticClass:"mb-5"},[_vm._v("De acuerdo con Calderón y Vallejo (2021), “la población estadística representa el conjunto de unidades de análisis con características comunes, so